<template>
    <div class="about-me">
        <div class="return">
          <span  @click="$emit('close-modal')" class="material-icons">
            arrow_upward
          </span>
        <h2>Hello there.</h2>
        <div class="about-me-text">
            <p> My name is Salvador and I'm a self-taught developer.</p>
            <p>I started watching Youtube tutorials and Udemy courses. Eventually I was able to create a <a href="https://github.com/salvadorAhumada" target="_blank">github repository</a> that caught the interest of the right people and gave me my first job in the industry.</p>
            <p>I'm always learning new things. What started as a career in frontend has slowly been turn into a career in fullstack developer</p>
            <p>Current stack: Vue, Express, Postgress. Transitioning to MERN stack</p>
        </div>
        </div>
        <h2>Portfolio</h2>
        <div class="portfolio-wrapper ">
            <h3 id="title" class="animated fadeIn faster" :key="paginatedProjects.title" >{{ paginatedProjects.title }}</h3>
                <div :key="paginatedProjects.key" class="portfolio">
                    <div class="arrow left">
                        <i v-if="page != 0" class="material-icons" @click="changePage(-1)">keyboard_arrow_left</i>
                    </div>
                    <div class="left animated fadeIn faster" id="left-section">
                        <a :href="paginatedProjects.url" target="_blank">
                            <img :src="paginatedProjects.image">
                        </a>
                    </div>
                    <div class="right animated fadeIn faster" id="right-section">
                        <p>{{ paginatedProjects.description}}</p>
                    </div>
                    <div class="arrow right">
                        <i v-if="page != projects.length - 1" class="material-icons arrows" @click="changePage(1)">keyboard_arrow_right</i>
                    </div>
                </div>
        </div>
    </div>
</template>

<script>

import aesthetic from '../../public/img/aesthetic.png';
import inbersus from '../../public/img/inbersus.png';
import msal from '../../public/img/msal.png';

export default {
    data() {
        return {
            page:0,
            projects:[
                {
                    title:'AestheticWave',
                    url:'https://salvadorahumada.github.io/aestheticwave/',
                    image:aesthetic,
                    description:'A web gallery that uses reddit API to fetch the latest images related to several design trends. Made with Vue. ',
                    key:1
                },
                {
                    title:'Inbersus',
                    url:'https://salvadorahumada.github.io/inbersus/',
                    image:inbersus,
                    description:'Frontend for a website aimed at financial independcy.Made with HTML, CSS, JS and jQuery.',
                    key:2
                },
                {
                    title:'My Super Awesome Library',
                    url:'https://my-awesome-library.herokuapp.com/',
                    image:msal,
                    description:'An exercise to understand the MERN stack. Made with React and Redux.',
                    key:3
                }
            ]
        }
    },
 methods:{
     changePage(p) {
         if(this.page === 0 && p === -1 || this.page === this.projects.length && p === 1)
         return;

         let leftElement = document.getElementById("left-section");
         let rightElement = document.getElementById("right-section");
         let title = document.getElementById("title");
         leftElement.classList.add("fadeOut");
         rightElement.classList.add("fadeOut");
         title.classList.add("fadeOut");
         
         setTimeout(() => {
            leftElement.classList.remove("fadeOut");
            rightElement.classList.remove("fadeOut");
            title.classList.remove("fadeOut");
            this.page = this.page + p;
         }, 600);

     }
 },
 computed: {
    paginatedProjects() {
        return this.projects[this.page]
    }
 }   
}
</script>

<style scoped>
    .about-me {
        position:fixed;
        left:0;
        right:0;
        top:0;
        bottom:0;
        font-family: 'Questrial', monospace;
        background:black;
        color:white;
        font-weight:lighter;
        overflow-y:auto;
    }

    .arrow {
        display: flex;
        align-items: center;
        cursor:pointer;
        width:66px;
    }

    .arrow i {
        font-size: 4rem;
    }

    h3 {
        font-family: 'Roboto Mono', monospace;
        font-weight: lighter;
    }

    .portfolio-wrapper {
        width:70%;
        margin:auto;
    }

    .portfolio {
        display: flex;
        justify-content: space-evenly;
    }

    #left-section,
    #right-section {
        width:40%;
        font-family: 'Roboto Mono', monospace;
    }

    .left img {
        width:100%;
    }

    .return h2 {
        font-weight: lighter;
    }

    .return {
        padding-top:30px;
    }

    .return span {
        cursor: pointer;
    }

    .about-me-text p {
        text-align:left;
        width:60%;
        max-width:800px;
        margin:20px auto;
        font-family: 'Roboto Mono', monospace;
    }

    .about-me-text p a {
        font-weight: bolder;
        font-family: 'Questrial', monospace;
        text-decoration: none;
        font-size: 17px;
        color: white;
    }

    a img {
        user-select: none;
    }

    @media screen and (max-width:700px) {
        .about-me-text p {
            text-align:left;
            width:90%;
            max-width:800px;
            margin:20px auto;
            font-family: 'Roboto Mono', monospace;
            }

    #left-section,
    #right-section {
        width:100%;
    }

    #right-section {
        margin-top:50px;
    }
        
        .portfolio {
            flex-direction: column;
            align-content: center;
        }

        .left {
            width:100%;
        }

        .portfolio {
            position: relative;
        }

        .arrow.left {
            position: absolute;
            left: -21%;
            top: 7px;
        }

        .arrow.right {
            margin:0;
            position: absolute;
            right: -21%;
            top: 31px;
        }

        .left {
            height: 114px;
        }

        .right {
            font-family: 'Roboto Mono', monospace;
            margin-top:20px;
        }
    }
</style>